import { Component, OnInit } from '@angular/core';
import { Question } from 'src/app/question';
import { ShareddataService } from 'src/app/shareddata.service';

@Component({
  selector: 'app-addquestions',
  templateUrl: './addquestions.component.html',
  styleUrls: ['./addquestions.component.css']
})
export class AddquestionsComponent implements OnInit {

  mainarray : Question[] = [] ; 
  ques : Question ; 
  tempans : string ; 
  constructor(private servicevar : ShareddataService) { }

  ngOnInit() {
  }

  submit(question : string , o1: string , o2 : string , o3 : string , o4 : string){
    this.ques = new Question; 
    this.ques.Question = question ; 
    this.ques.Option1 = o1
    this.ques.Option2 = o2; 
    this.ques.Option3 = o3; 
    this.ques.Option4 = o4; 
    this.ques.Answer = this.tempans; 
    this.mainarray.push(this.ques); 
  }

  viewonConsole() {
    console.log (this.mainarray);
  }

  saveans(answer: string ){
  console.log(answer)
    this.tempans = answer; 
    }

  savearray(){
    this.servicevar.setArr(this.mainarray); 
  }
}
