import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { UserLandingComponent } from './user/user-landing/user-landing.component';
import { AdminLandingComponent } from './admin/admin-landing/admin-landing.component';
import { AdminhomepageComponent } from './admin/adminhomepage/adminhomepage.component';
import { AddquestionsComponent } from './admin/addquestions/addquestions.component';
import { UserquestionComponent } from './user/userquestion/userquestion.component';


const routes: Routes = [
  {path : '' , component : UserLandingComponent} ,  
  {path : 'admin' , component : AdminLandingComponent} , 
  {path : 'user' , component : UserLandingComponent} , 
  {path : 'adminhome' , component : AdminhomepageComponent}, 
  {path : 'adminhome/addquestion' , component : AddquestionsComponent} , 
  {path : 'viewq' , component : UserquestionComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
