import { Component, OnInit } from '@angular/core';
import { ShareddataService } from 'src/app/shareddata.service';
import { Question } from 'src/app/question';

@Component({
  selector: 'app-userquestion',
  templateUrl: './userquestion.component.html',
  styleUrls: ['./userquestion.component.css']
})
export class UserquestionComponent implements OnInit {
  quesarray : Question[] =[];  
  correctans : boolean = false; 
  wrongans : boolean = false; 
  savedans : any ; 
 
  constructor(private servar : ShareddataService) { }

  ngOnInit() { 
    this.quesarray = this.servar.getQuestions() ;
    console.log(this.quesarray);
  }
  checkans(quest : Question) {
  console.log(this.savedans);
  if(this.savedans == quest.Answer)
      this.correctans = true; 
  else
    this.wrongans = true; 

  }

reset() {
  this.savedans = null; 
  this.correctans = false; 
  this.wrongans = false;
 
}
}
